<template>
  <div class="state">
    <the-navbar />
    <main class="main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12 col-sm-8 col-lg-9">
            <div class="row">
              <div class="col-12">
                <Skyler
                  :cenefa="state.cenefa"
                  :image="state.image"
                  :name="getState.name"
                />
              </div>
              <div
                class="col-12 col-sm-6 col-lg-4"
                v-for="(item, index) in getPagesByState"
                :key="index"
              >
                <Site :image="item.image" :name="item.name" :slug="item.slug" />
              </div>
              <div class="col-12 col-lg-3"></div>
            </div>
          </div>
          <div class="col-12 col-sm-4 col-lg-3">
            <div class="row">
              <div class="col-12">
                <Adds />
              </div>
              <div class="col-12">
                <Featureds />
              </div>
              <div class="col-12">
                <Adds />
              </div>
              <div class="col-12">
                <the-footer />
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import Adds from "@/components/Adds";
import Featureds from "@/components/Featureds";
import Site from "@/components/Site";
import Skyler from "@/components/Skyler";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

export default {
  name: "State",
  components: {
    Adds,
    Featureds,
    Site,
    Skyler,
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      state: {
        cenefa: "true",
        image: "",
        name: ""
      }
    };
  },
  created() {
    this.fetchState(this.$route.params.state);
    this.fetchPagesByState(this.$route.params.state);
  },
  methods: {
    ...mapActions(["fetchState", "fetchPagesByState"])
  },
  computed: {
    ...mapGetters(["getState", "getPagesByState"])
  },
  watch: {
    $$route: "getPagesByState"
  }
};
</script>

